import React from 'react';
import { unitsConverter } from '@ali/4ever-utils';
import { DEFAULT_STICKERS, StickerItem, StickerManager } from '@ali/4ever-plugin-sticker';

let sticker;

export function getStickerSymbolWidth(size: string) {
  const sz = parseInt(size, 10);
  return unitsConverter.toPX(`${(sz > 0 ? sz : 10) * 1.5}pt`);
}

export default function getStickerSymbol(code: string, size = '10pt') {
  if (!sticker) {
    sticker = new StickerManager({ customSticker: [DEFAULT_STICKERS] });
  }
  return sticker.getInfoByCode(code) ? (
    <StickerItem
      code={code}
      sz={getStickerSymbolWidth(size)}
      sticker={sticker}
    />
  ) : null;
}
